* {
    font-family: sans-serif;
    
}

.grid-area{
    margin-top: 30px;
    table{
        max-width: 700px;
        margin: auto;
        user-select: none;

        td{
            width: 20px;
            height: 20px;
            text-align: center;

            &.selected{
                color: rgb(214, 93, 37);
            }
        }
    }
    
}